import React, {useState} from 'react'
import './App.css';

const App = () => {
  const pics = ['pics/0.jpg', 'pics/1.jpg', 'pics/2.jpg', 'pics/3.jpg', 'pics/4.jpg']
  const [cond, setCond] = useState('0')
  
  
  const renderThis = (e) => {
    setCond(e.currentTarget.value)
    console.log(cond);
  }

  

  

  return (
    <>
    <h1>React-Slider App</h1>
    <div className="root">
        <img src={pics[0]} alt="" style={{marginLeft: cond + 'px'}} />
        <img src={pics[1]} alt="" style={{marginLeft: cond}} />
        <img src={pics[2]} alt="" style={{marginLeft: cond}} />
        <img src={pics[3]} alt="" style={{marginLeft: cond}} />
        <img src={pics[4]} alt="" style={{marginLeft: cond}} />
    </div>
    
    <div className="inputs">
      <label>
          <input type="radio" name="radio-btn" id="radio0" value="0" onChange={renderThis} defaultChecked />
      </label>

      <label>
          <input type="radio" name="radio-btn" id="radio1" value="-800" onChange={renderThis} />
      </label>

      <label>
          <input type="radio" name="radio-btn" id="radio2" value="-1600" onChange={renderThis} />
      </label>

      <label>
          <input type="radio" name="radio-btn" id="radio3" value="-2400" onChange={renderThis} />
      </label>  
        
      <label>
          <input type="radio" name="radio-btn" id="radio4" value="-3200" onChange={renderThis} />
      </label>  
    </div>
    
    </>
  )
}

export default App;
